@charset "UTF-8";
/* css reset */
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

*,
*::after,
*::before {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -o-box-sizing: border-box;
  -ms-box-sizing: border-box;
  font-family: "Microsoft JhengHei" !important; }

.header {
  background-image: url("../images/Hero.jpg");
  height: 380px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-size: cover;
  background-position: center center; }

.content {
  background-color: #f5f5f5;
  display: flex;
  flex-direction: column;
  align-items: center; }

.container {
  width: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center; }
  @media (max-width: 2000px) {
    .container {
      max-width: 960px; } }

.footer {
  height: 90px;
  background-color: rgba(85, 154, 200, 0.85);
  justify-content: center;
  display: flex;
  flex-direction: column;
  align-items: center; }

.kTravel-footer {
  font-size: 0.875rem;
  color: #ffffff; }

.kTravel-source {
  margin-top: 15px;
  font-size: 0.875rem;
  color: #f5d005; }

.kTravel-title {
  width: 100%;
  display: block;
  font-size: 80px;
  color: #ffffff;
  font-weight: bold;
  padding: 65px 0;
  text-align: center; }
  @media (max-width: 769px) {
    .kTravel-title {
      text-align: center;
      font-size: 60px; } }
  .kTravel-title span {
    font-size: 80px; }
    @media (max-width: 769px) {
      .kTravel-title span {
        display: block;
        font-size: 50px 0; } }

.kTravel-select {
  width: 30%;
  background-color: rgba(255, 255, 255, 0.7);
  border: 3px solid #ffffff;
  border-radius: 3px;
  margin-bottom: 20px;
  padding: 15px;
  font-size: 1.35rem;
  font-weight: bold;
  color: #000000;
  cursor: pointer;
  /* 移除箭頭樣式 */
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none; }
  @media (max-width: 769px) {
    .kTravel-select {
      width: 100%; } }
  .kTravel-select:focus {
    outline: none; }

.districtContent {
  width: 100%;
  margin-bottom: 39px;
  display: flex;
  justify-content: center;
  align-content: center;
  flex-wrap: wrap; }

.districtName {
  font-size: 36px;
  color: #8a82cc;
  text-align: center;
  text-transform: uppercase; }

.hotDistrict {
  margin-top: -28px;
  width: 100%;
  background-color: #ffffff;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.3);
  border-radius: 5px; }
  @media (max-width: 769px) {
    .hotDistrict {
      width: 100%;
      height: 100%;
      z-index: 2; } }

.hotDistrict-title {
  font-size: 1.125rem;
  color: #000000;
  font-weight: bold;
  margin: 20px 0;
  text-align: center; }

.hotDistrict-list {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px 25px; }
  @media (max-width: 769px) {
    .hotDistrict-list {
      flex-wrap: wrap;
      padding: 20px;
      padding-top: 0; } }
  .hotDistrict-list-item {
    width: 100%;
    text-align: center;
    margin-left: 20px;
    margin-right: 20px;
    border-block-start: 3px solid;
    border-block-end: 3px solid; }
    @media (max-width: 769px) {
      .hotDistrict-list-item {
        margin-right: 0px;
        margin-left: 0px;
        margin-top: 20px; } }
  .hotDistrict-list-link {
    display: block;
    text-decoration: none;
    font-size: 20px;
    padding: 15px 0;
    font-weight: bold; }
    @media (max-width: 769px) {
      .hotDistrict-list-link {
        display: block;
        width: 100%;
        font-size: 1.6875rem; } }

/* Card */
.card {
  width: 464px;
  height: 100%;
  margin-top: 36px;
  margin-left: 8px;
  margin-right: 8px;
  transition: all 0.3s ease; }
  .card:hover {
    padding: 10px; }
  @media (max-width: 769px) {
    .card {
      width: 100%;
      margin-top: 20px;
      margin-left: 0px;
      margin-right: 0px; } }

.card-header {
  width: 100%;
  height: 200px;
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  padding: 14px 22px;
  background-size: cover;
  background-position: center center;
  border-top-left-radius: 45px; }
  .card-header-title {
    font-size: 30px;
    color: rgba(255, 255, 255, 0.9);
    font-weight: bold; }
  .card-header-content {
    font-size: 1.125rem;
    color: rgba(255, 255, 255, 0.9); }

.card-body {
  /* height: 118px; */
  height: 100%;
  background: #ffffff;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.2);
  border-bottom-right-radius: 45px;
  padding-right: 23px;
  padding-left: 23px;
  display: flex;
  flex-direction: column;
  justify-content: center; }

.districtInfo {
  padding-top: 16px;
  display: flex;
  align-items: center;
  /* 增加 div 控制圖片外圍寬高 */ }
  .districtInfo-content {
    display: block;
    font-size: 1rem;
    color: #000000;
    margin-left: 9px;
    line-height: 1.5;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis; }
  .districtInfo-img {
    width: 20px;
    height: 20px;
    text-align: center;
    padding: 2px; }

.positionSet {
  display: flex;
  justify-content: space-between;
  padding-bottom: 19px; }

.separation {
  width: 900px;
  margin-top: 37px;
  margin-bottom: 65px; }
  @media (max-width: 769px) {
    .separation {
      width: 100%; } }

.separation-line {
  border: 1px groove #000000;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  animation: change 5s infinite; }
  .separation-line::after {
    content: "";
    position: absolute;
    width: 24px;
    height: 24px;
    border: 2px solid #000000;
    border-radius: 50%;
    animation: change 5s infinite; }
  .separation-line-img {
    position: absolute;
    z-index: 1;
    animation: spin 4s linear infinite; }

/* animation change color */
@keyframes change {
  0% {
    border: 2px solid #559ac8;
    background: #559ac8; }
  20% {
    border: 2px solid #8a82cc;
    background: #8a82cc; }
  60% {
    border: 2px solid #f5d005;
    background: #f5d005; }
  80% {
    border: 2px solid #ffa782;
    background: #ffa782; }
  100% {
    border: 2px solid #559ac8;
    background: #559ac8; } }

/* animation 旋轉 */
@keyframes spin {
  50% {
    transform: rotateY(360deg); } }

/* goTop */
.goTop {
  display: none;
  position: fixed;
  bottom: 75px;
  right: 38px;
  cursor: pointer; }
  @media (max-width: 769px) {
    .goTop {
      bottom: 50px;
      right: 25px; } }

/* pagination */
.page {
  display: flex;
  margin-top: 40px;
  margin-bottom: 40px;
  flex-wrap: wrap;
  justify-content: center; }
  @media (max-width: 769px) {
    .page {
      width: 100%;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      display: flex;
      justify-content: space-between;
      z-index: 1; } }

.page-direction {
  text-align: center;
  margin-left: 20px;
  margin-right: 20px; }
  @media (max-width: 769px) {
    .page-direction {
      margin-left: 0px;
      margin-right: 0px; } }
  .page-direction-link {
    display: block;
    padding: 5px;
    font-size: 0.875rem;
    color: #4a4a4a;
    text-decoration: none; }
    @media (max-width: 769px) {
      .page-direction-link {
        height: 100%;
        font-size: 1.6875rem;
        background-color: transparent;
        color: #ffffff;
        padding: 10px; }
        .page-direction-link:hover {
          background-color: rgba(0, 0, 0, 0.5); } }

.page-item {
  text-align: center;
  margin-left: 4px;
  margin-right: 4px; }
  @media (max-width: 769px) {
    .page-item {
      display: none; } }

.page-link {
  width: 25px;
  height: 25px;
  border-radius: 50%; }
  .page-link:hover {
    color: #ffffff;
    transition: all 0.4s; }
  .page-link.active {
    pointer-events: none;
    cursor: default;
    background-color: #559ac8;
    border-radius: 50%;
    color: #ffffff;
    font-weight: bold; }
